.fadeInUp,
.slideInUp,
.fadeInRight,
.fadeInLeft {
	animation-duration: 800ms;
}



@keyframes menu-show {
	from {
		display: block;
		transform: translateY(1rem);
		opacity: 0;
	}

	to {
		transform: translateY(0);
		opacity: 1;
	}
}

.card-show {
	animation-duration: 1s;
	animation-name: card-show-anime;
}

@keyframes card-show-anime {

	from {
		transform: translateY(4rem);
		@include box_shadow(4);
	}

	80% {
		transform: translateY(0);
		@include box-shadow(4);
	}

	to {
		@include box-shadow(1);
	}

}

.left-reveal {
	animation-duration: 1s;
	animation-name: left-reveal-anime;
}

@keyframes left-reveal-anime {
	from {
		transform: translateX(-3rem);
		opacity: 0.5;
	}

	to {
		transform: translateX(0rem);
		opacity: 1;
	}	
}

.subtle-fadeUp {
	animation-duration: 500ms;
	animation-name: subtle-fadeUp-anime;
}

@keyframes subtle-fadeUp-anime {
	from {
		opacity: 0;
		transform: translateY(4rem);
	}	
	to {
		opacity: 1;
		transform: translateY(0rem);
	}	
}